import React, { Component } from 'react';

class Header extends Component {
    render() {
        return (
            <div className=''>
                <div className="carousel slide" data-ride="carousel"  id="carouselExampleIndicators" >
                    <ol className="carousel-indicators">
                        <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                        <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                        <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                    </ol>
                    <div className="carousel-inner">
                        <div className="carousel-item active">
                            <img
                                className="img-fluid"
                                src="https://cdnb.artstation.com/p/assets/images/images/000/143/037/large/ivan-smirnov-outlaws.jpg"
                                alt="First slide"></img>
                        </div>
                        <div className="carousel-item">
                            <img
                                className="d-block w-100"
                                src="https://cdna.artstation.com/p/assets/images/images/000/143/040/large/ivan-smirnov-science-can-be-fun.jpg"
                                alt="Second slide"></img>
                        </div>
                        <div className="carousel-item">
                            <img
                                className="d-block w-100"
                                src="https://cdnb.artstation.com/p/assets/images/images/000/143/251/large/ivan-smirnov-mercenaries.jpg"
                                alt="Third slide"></img>
                        </div>
                    </div>
                    <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button"
                       data-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="sr-only">Previous</span>
                    </a>
                    <a className="carousel-control-next" href="#carouselExampleIndicators" role="button"
                       data-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="sr-only">Next</span>
                    </a>
                </div>
            </div>
        )
    }
}

export default Header;


